.header {
  position: absolute;
  background-color: hsl(0, 0%, 0%, 0.5);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 66px;
  box-sizing: border-box;
  z-index: 100;
  padding: 0 20px;
  border-bottom: 1px solid transparent;


  @media (min-width: $tablet) {
    height: 110px;
  }

  @media (min-width: $desktop) {
    max-width: 1200px;
    background-color: transparent;
    align-self: center;
    padding: 0 130px;
  }
}

